a {
    text-decoration: none;
  }
  
  .logo {
    width: 60px;
    height: 30px;
    margin-right: 20px;
  }
  
  .carousel-item img {
    width: 80%;
    height: 500px;
    margin: auto;
    object-fit: contain;
  }
  
  .ud-link-hover:hover {
    color: rgb(204, 12, 12)
  }
  
  .ud-footer-background {
    background-color: rgb(206, 221, 221);
  }
  
  .ud-justify {
    text-align: justify;
  }
  
  .ud-founder-img {
    /* object-fit: contain; */
    width: 300px;
    height: 250px;
  }
  
  .small-about-image {
    width: 100px;
    height: 100px;
    border-radius: 15%;
  }
  
  .image-custom-css {
    width: 200px;
    height: 200px;
    border-radius: 0% 3% 3% 0%;
  }
  .ud-card-box {
    border-top: 15px solid brown;
    border-left: 11px solid brown;
    width: 200px;
    height: 300px;
    border-radius: 5%;
  }
  
  .firstcharacter {
  
    float: left;
    font-family: Georgia;
    font-size: 75px;
    line-height: 40px;
    padding-top: 4px;
    padding-right: 8px;
    padding-left: 3px;
  }
  
  .first-character {
    float: left;
    width: 0.7em;
    font-size: 350%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    line-height: 80%;
  }
  
  .ud-landscape-image {
    width: 350px;
    height: 250px;
    border-radius: 5px;
  }
  
  .active_styles {
    border-bottom: 2px solid #000;
  }
  
